import s from './Question.module.css';

import plus from '../../../image/plus.svg'
import { useState } from 'react';


const Question = (props) => {

const [edit, setEdit] = useState(false)


    const plusRotaite =(e)=> {
       
        
        if (edit) {
            document.getElementById('plus').style.transform =  ''
            setEdit(false)
        }
        else {
            document.getElementById('plus').style.transform =  'rotate(45deg)'
            setEdit(true)
        }

    }


    const arr = [
       
        {name: '   ', id: 'plus2'},
        {name: '        ', id: 'plus3'},
        {name: '   ', id: 'plus4'},
        {name: '    ', id: 'plus5'},
        {name: ' ,   ', id: 'plus6'},
        {name: '  ',id: 'plus7'},

    ]




    return (
        <div className={s.question}>
            <div className={s.docTitle}>-</div>
            <div className={s.questionList}>
               
                   
                        <div className={s.questionListItem} >
                        <img alt='' src={plus} className={s.plus} id='plus' 
                        style={{ marginRight: '10px', cursor: 'pointer' }} 
                        onClick={plusRotaite}></img>
                          
                        {
                            edit
                                ? <div className={s.listD}>
                                    <ol>
                                        <li>     </li>
                                        <li>  
                                              .</li>
                                        <li>,     
                                              .</li>
                                        <li>    
                                              . 
                                                
                                            ()  .</li>
                                    </ol>
                                </div>
                                : null
                        }
                    </div>
                    
               
                   
                {arr.map(a=> {
                    return (
                        <div className={s.questionListItem1} key={a.name}>
                        <img alt='' src={plus}   style={{ marginRight: '10px' }}></img>
                       {a.name}
                    </div>
                    )
                })
                   
                }
                
                
            </div>

        </div>



    )
}

export default Question;
